import React from 'react';
import _ from 'lodash';
import Head from 'next/head';
import RadioButtonGroup from '../components/RadioButtonGroup';
import data from '../db/albion';

class Home extends React.Component {
  state = { Categories: {}, SubCategories: {}, Category: '', SubCategory: '' };

  componentDidMount() {
    this.setState({ Categories: data });
  }

  //I want to find a way to re-use this event for navigating further
  //into an object without having to make another helper method
  handleCategoryChange = event => {
    console.log(event.target);

    const { childObj, currentObj, value } = event.target;
    this.setState({ [childObj]: currentObj[value] });
  };

  render() {
    const { Categories, Category } = this.state;

    return (
      <div>
        <Head>
          <title>Albion Tools</title>
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>

        <RadioButtonGroup
          data={Categories}
          handleCategoryChange={this.handleCategoryChange}
          value={Category}
          currentObj="Categories"
          childObj="SubCategories"
        />
        {this.state.Category && (
          <RadioButtonGroup
            data={this.state.SubCategories}
            handleCategoryChange={this.handleCategoryChange}
            value={this.state.SubCategory}
          />
        )}
        {/* NOTE: ([name]) is array destruturing, it takes the first element of the
        arrays and names it so we can properly identify it for filtering, remember 
        the name is arbitrary */}
      </div>
    );
  }
}

export default Home;
